import React from "react"
import { FaRegEdit, FaRegTrashAlt, FaCircle } from "react-icons/fa"
import { Card, Tooltip, OverlayTrigger } from "react-bootstrap"
import { Link } from "gatsby"

import UserLogged from "../../../utils/UserLogged"

import { DateText } from "../style"

interface IProps {
	id: number
	title: string
	slug: string
	article: string
	published: boolean
	createdAt: string
	language: number
	isNews: boolean
	isLegislation: boolean
	postImage: {
		id: number
		imageUrl: string
	}
	isFirst?: boolean
	editArticle(id: number): void
	deleteArticle(id: number): void
}

export default function ArticleCard({
	editArticle,
	deleteArticle,
	id,
	title,
	slug,
	createdAt,
	published,
	postImage,
	isFirst,
	language,
	isNews,
	isLegislation,
}: IProps) {
	return (
		<Card style={{ width: "100%", height: "100%" }}>
			<UserLogged>
				<Card.Header>
					<small className="text-muted float-left">
						<OverlayTrigger
							placement={"top"}
							overlay={
								<Tooltip id="tooltip-post">
									{published ? "Publicado" : "Não publicado"}
								</Tooltip>
							}
						>
							<FaCircle color={published ? "green" : "red"} />
						</OverlayTrigger>
					</small>
					<small className="text-muted float-right">
						<FaRegEdit size={20} onClick={() => editArticle(id)} />
						<FaRegTrashAlt
							onClick={() => deleteArticle(id)}
							className="ml-3"
							size={20}
							color={"#CD7165"}
						/>
					</small>
				</Card.Header>
			</UserLogged>
			<Link
				to={
					isLegislation
						? `/legislation-articles/${slug}`
						: isNews
						? `/news/${slug}`
						: `/articles/${slug}`
				}
			>
				<Card.Img
					style={{ height: isFirst ? "auto" : "160px", objectFit: "cover" }}
					variant="top"
					src={postImage.imageUrl}
				/>
				<Card.Body>
					<DateText>
						{new Intl.DateTimeFormat(language == 0 ? "en-US" : "pt-BR", {
							year: "numeric",
							month: "long",
						}).format(new Date(createdAt))}
					</DateText>
					<Card.Title style={{ marginBottom: 0 }}>{title}</Card.Title>
				</Card.Body>
			</Link>
		</Card>
	)
}
