import React, { useRef, useState, useEffect } from "react"
import { Form, Button } from "react-bootstrap"
import { Modal } from "react-responsive-modal"
import JoditEditor from "jodit-react"

import PostService from "../../../../../services/post-service"
import convertImageToBase64 from "../../../../utils/convertImageToBase64"
import { useInput } from "../../../../../hooks/input-hook"
import { IArticleFormData, IArticles } from "../../../../../interfaces"

interface IProps {
	dataModal: IArticleFormData
	showModal: boolean
	onCloseModal(): void
	handleSubmit(data: any): void
	handlePreview(data: any): void
	isLegislationForm?: boolean
}

export default function ModalForm(props: IProps) {
	const {
		dataModal,
		showModal,
		onCloseModal,
		handleSubmit,
		handlePreview,
	} = props
	const editor = useRef(null)

	const {
		value: title,
		setValue: setTitle,
		bind: bindTitle,
		reset: resetTitle,
	} = useInput()
	const {
		value: article,
		setValue: setArticle,
		bind: bindArticle,
		reset: resetArticle,
	} = useInput()
	const [articles, setArticles] = useState<IArticles[]>([])
	const [published, setPublished] = useState<boolean>(false)
	const [image, setImage] = useState("")
	const [imageBase64, setImageBase64] = useState<string | ArrayBuffer | null>()
	const [isPT, setLanguage] = useState<boolean>(false)
	const [isNews, setNews] = useState<boolean>(false)
	const [isLegislation, setLegislation] = useState<boolean>(false)
	const [linkId, setLinkId] = useState<number | undefined>(undefined)

	const postService = new PostService()

	async function getArticles() {
		postService.listAll<IArticles[]>().then(response => {
			setArticles(response)
		})
	}

	useEffect(() => {
		getArticles()
		if (dataModal && dataModal.id) {
			setTitle(dataModal.title)
			setArticle(dataModal.article || "")
			setImageBase64(dataModal.image)
			setPublished(dataModal.published)
			setLanguage(dataModal.language == 1)
			setNews(dataModal.isNews)
			setLinkId(dataModal.linkId)
			setLegislation(dataModal.isLegislation)
		} else {
			resetTitle()
			resetArticle()
			setPublished(false)
			setImage("")
			setImageBase64("")
			setLanguage(false)
			setNews(false)
			setLinkId(undefined)
			setLegislation(!!props.isLegislationForm)
		}
	}, [])

	function handleImage(event: any) {
		setImage(event.target.value)
		const file = event.target.files[0]

		convertImageToBase64(file).then(base64 => setImageBase64(base64))
		dataModal.imageId = undefined
	}

	function handlePublished(event: any) {
		setPublished(event.target.checked)
	}

	function handleLanguageEN(event: any) {
		setLanguage(false)
	}

	function handleLanguagePT(event: any) {
		setLanguage(true)
	}

	function handleNews(event: any) {
		setNews(event.target.checked)
	}

	function handleLinkArticle(event: any) {
		const { value } = event.target
		setLinkId(value != 0 ? value : null)
	}

	const save = async (event: any) => {
		event.preventDefault()
		await handleSubmit({
			id: dataModal.id,
			title,
			image: imageBase64,
			article,
			published,
			isNews,
			language: isPT ? 1 : 0,
			linkId,
			isLegislation: props.isLegislationForm,
		})

		resetArticle()
		resetTitle()
		onCloseModal()
	}

	function preview() {
		handlePreview({
			id: "preview",
			title: title,
			article: article,
			published,
			postImage: {
				imageUrl: dataModal.imageUrl,
			},
		})
	}

	return (
		<Modal open={showModal} onClose={onCloseModal} center>
			<Form onSubmit={save} style={{ minWidth: "700px" }}>
				<h3>Adicionar artigo</h3>

				<Form.Group>
					<Form.Label>Título</Form.Label>
					<Form.Control type="text" {...bindTitle} />
				</Form.Group>

				<Form.Group>
					<Form.Label>Imagem principal</Form.Label>
					<Form.Control type="file" value={image} onChange={handleImage} />
				</Form.Group>

				<Form.Group>
					<Form.Label>Conteúdo</Form.Label>
					<JoditEditor ref={editor} {...bindArticle} />
				</Form.Group>

				<Form.Group>
					<Form.Label>Vincular tradução</Form.Label>
					<Form.Control
						as="select"
						custom
						onChange={handleLinkArticle}
						value={linkId}
					>
						<option value={0}>Nenhum</option>
						{articles.map(article => (
							<option value={article.id} key={article.id}>
								{article.title}
							</option>
						))}
					</Form.Control>
				</Form.Group>

				<Form.Check
					type="radio"
					label="PT"
					name="formHorizontalRadios"
					id="formHorizontalRadios1"
					checked={isPT}
					onChange={handleLanguagePT}
				/>
				<Form.Check
					type="radio"
					label="EN"
					name="formHorizontalRadios"
					id="formHorizontalRadios2"
					checked={!isPT}
					onChange={handleLanguageEN}
				/>
				{!props.isLegislationForm && (
					<Form.Check
						type="switch"
						label="é news"
						id="custom-switch-news"
						className="mb-4"
						checked={isNews}
						onChange={handleNews}
					/>
				)}

				<Form.Check
					type="switch"
					label="Publicar artigo"
					id="custom-switch"
					className="mb-4"
					checked={published}
					onChange={handlePublished}
				/>

				<Button variant="primary" type="submit" className="mr-2">
					Salvar
				</Button>
				<Button variant="link" type="button" onClick={preview}>
					Visualizar
				</Button>
			</Form>
		</Modal>
	)
}
