import styled from "styled-components"

export const Container = styled.div`
	max-width: 750px;
	width: 100%;
	margin-bottom: 64px;
	position: relative;

	img {
		width: 100%;
	}

	.wrapper-image-cover {
		border-radius: 16px;
		overflow: hidden;
		margin-top: -56px;
		max-height: 450px;
	}
`

export const Content = styled.div`
	text-align: left;
	color: #444;
	font-family: "Montserrat", Sans-serif;
	font-size: 20px;
	font-weight: 400;
	line-height: 1.6;
	overflow: hidden;
	width: 100%;

	ul,
	ol {
		padding-inline-start: 40px;
	}
`

export const Sidebar = styled.div`
	position: absolute;
	top: 80px;
	right: -310px;
	width: 300px;

	@media (max-width: 1400px) {
		width: 260px;
		right: -275px;
	}

	@media (max-width: 1330px) {
		display: none;
	}
`
export const ImageLink = styled.div`
	padding: 5%;
`
