import styled from "styled-components"

export const Container = styled.div`
	margin-bottom: 40px;

	a {
		color: #333;
	}
	a:hover {
		text-decoration: none;
	}

	.col-md-8 {
		img.card-img-top {
			height: 375px;
			object-fit: cover;
		}

		.card-body > span > .react-loading-skeleton {
			height: 375px !important;
		}
	}

	.wrapper-facebook {
		padding: 0;
		text-align: center;

		@media (max-width: 576px) {
			display: none;
		}
	}
`

export const DateText = styled.span`
	font-size: 12px;
	text-transform: uppercase;
	color: #aaa;
	display: inline-block;
	margin-bottom: 8px;
`
