import React, { useEffect, useState } from "react"
import { FacebookProvider, Page } from "react-facebook"

import Banner from "../../utils/Banner"
import ShareButtons from "../../utils/ShareButtons"

import { Container, Content, ImageLink, Sidebar } from "./style"
import { IArticles } from "../../../interfaces"
import PostService from "../../../services/post-service"
import Skeleton from "react-loading-skeleton"
import SEO from "../../utils/SEO"
import { navigateTo } from "gatsby"

interface IProps {
	path: string
	id?: string
	titleCallback?: (title?: string) => void
}

function ArticleComponent({ id, titleCallback }: IProps) {
	const postService = new PostService()
	const [article, setArticle] = useState({} as IArticles)
	const facebookData = {
		id: "731372470600932",
		url: "https://www.facebook.com/kivalitaconsulting/",
	}

	useEffect(() => {
		async function getArticle() {
			const lang = localStorage.getItem("i18nextLng") == "en" ? "en" : "pt"

			const articleResponse = await postService.getPreview()

			console.log(articleResponse)

			if (articleResponse) {
				setArticle(articleResponse)
				titleCallback(articleResponse.title)
			} else {
				navigateTo("/articles")
			}
		}

		getArticle()
	}, [])

	const openLandingPage = () => {
		window.open(
			"https://conteudo.kivalitaconsulting.com/e-book-iso-27001",
			"_blank"
		)
	}

	if (article && !article.id) {
		return (
			<>
				<Skeleton height={300} />
				<div className="container mt-3 mb-3">
					<Skeleton count={3} />
					<div className="row mt-3">
						<div className="col-image col-6" style={{ textAlign: "center" }}>
							<Skeleton count={1} height={200} width={200} />
						</div>
						<div className="col-6">
							<Skeleton count={15} />
						</div>
					</div>
				</div>
			</>
		)
	}
	return (
		<>
			<SEO title={article.title} article={true} />
			<Banner titlePage={article.title} />
			<Container className="container">
				<div className="wrapper-image-cover">
					<img
						alt="Article image"
						width="100%"
						src={article.postImage.imageUrl}
					/>
				</div>
				<ShareButtons url={window.location.href} />

				<Content dangerouslySetInnerHTML={{ __html: article.article }} />

				<Sidebar>
					<FacebookProvider appId={facebookData.id}>
						<Page href={facebookData.url} tabs="timeline" />
					</FacebookProvider>
					<ImageLink onClick={openLandingPage}>
						<img
							alt="Download Livro Kivalita"
							width="500px"
							src="https://www.kivalitaconsulting.com/wp-content/E-Book_1080x1080.png"
						/>
					</ImageLink>
				</Sidebar>
			</Container>
		</>
	)
}

export default ArticleComponent
