import Service from "./service"
import { IArticleFormData, IArticles } from "../interfaces"

export enum LanguagePost {
	en = 0,
	pt = 1,
}

class PostService {
	private api = new Service()

	public listAll<T>(): Promise<T> {
		return this.api.get("post/v2")
	}

	public list<T>(language?: LanguagePost): Promise<T> {
		const queryString = language != undefined ? `,language==${language}` : ""
		return this.api.get(
			"post/v2?filters=isNews==false,isLegislation==false" + queryString
		)
	}

	public listNews<T>(language?: LanguagePost): Promise<T> {
		const queryString = language != undefined ? `,language==${language}` : ""
		return this.api.get(
			"post/v2?filters=isNews==true,isLegislation==false" + queryString
		)
	}

	public listLegislation<T>(language?: LanguagePost): Promise<T> {
		const queryString = language != undefined ? `,language==${language}` : ""
		return this.api.get("post/v2?filters=isLegislation==true" + queryString)
	}

	public get<T>(id: number): Promise<T> {
		return this.api.get(`post/${id}`)
	}

	public getByLanguage<T>(id: number, lang: string): Promise<T> {
		return this.api.get(`post/${id}/${lang}`)
	}

	public delete(id: number): Promise<void> {
		return this.api.delete(`post/${id}`)
	}

	public create({
		title,
		image,
		article,
		published,
		userId = "0",
		language,
		isNews,
		linkId,
		isLegislation,
	}: IArticleFormData) {
		return this.api.post("post", {
			title,
			article: article,
			published,
			authorId: userId,
			imageData: image,
			language,
			isNews,
			linkId,
			isLegislation
		})
	}

	public update({
		id,
		title,
		image,
		imageId,
		article,
		published,
		userId = "0",
		language,
		isNews,
		linkId,
		isLegislation,
	}: IArticleFormData) {
		return this.api.put(`post/${id}`, {
			id,
			title,
			imageId,
			article: article,
			imageData: image,
			published,
			authorId: userId,
			language,
			isNews,
			linkId,
			isLegislation,
		})
	}

	public savePreview(article: IArticles) {
		if (typeof localStorage != "undefined") {
			localStorage.setItem("postPreview", JSON.stringify(article))
		}
	}

	public getPreview(): IArticles | undefined {
		if (typeof localStorage != "undefined") {
			const article = localStorage.getItem("postPreview")

			if (!article) {
				return undefined
			}

			return JSON.parse(article)
		}
	}
}

export default PostService
