import React, { useState, useEffect, useCallback } from "react"
import { FacebookProvider, Page } from "react-facebook"
import { Row, Col } from "react-bootstrap"
import { FaPlus } from "react-icons/fa"

import PostService, { LanguagePost } from "../../../services/post-service"
import { IArticles, IArticleFormData } from "../../../interfaces"
import Banner from "../../utils/Banner"
import ButtonBox from "../../utils/ButtonBox"
import UserLogged from "../../utils/UserLogged"
import ModalForm from "./components/ModalForm"
import ArticleCard from "./components/ArticleCard"
import ArticleCardLoad from "./components/ArticleCardLoad"

import { Container } from "./style"
import { useAuth } from "../../../hooks/auth"
import { translate } from "../../utils/translate"

interface IProps {
	path: string
	isNews: boolean
	isLegislation?: boolean
	titleCallback: (title?: string) => void
}

const ArticlesComponent: React.FC<IProps> = props => {
	const { user } = useAuth()
	const [dataModal, setDataModal] = useState({} as IArticleFormData)
	const [showModal, setShowModal] = useState(false)
	const [articles, setArticles] = useState<IArticles[]>([])
	const [firstArticle, setFirstArticle] = useState<IArticles>()
	const [loading, setLoading] = useState(false)
	const postService = new PostService()
	const isNews = props.isNews
	const isLegislation = props.isLegislation

	const facebookData = {
		id: "731372470600932",
		url: "https://www.facebook.com/kivalitaconsulting/",
	}

	const openModal = () => setShowModal(true)
	const closeModal = () => {
		setShowModal(false)
		setDataModal({} as IArticleFormData)
	}

	const editArticle = async (id: number) => {
		const articleFound = await postService.get<IArticles>(id)
		if (articleFound) {
			setDataModal({
				id: articleFound.id,
				title: articleFound.title,
				imageId: articleFound.postImage.id,
				article: articleFound.article,
				published: articleFound.published,
				language: articleFound.language,
				isNews: articleFound.isNews || false,
				linkId: articleFound.linkId,
				userId: articleFound?.author?.id,
				isLegislation: articleFound.isLegislation || false,
			})

			openModal()
		}
	}

	const deleteArticle = useCallback(
		async (id: number) => {
			postService.delete(id).then(() => {
				const jobsFilter = articles.filter(article => article.id !== id)
				setArticles(jobsFilter)
			})
		},
		[articles]
	)

	async function getArticles() {
		const language =
			localStorage.getItem("i18nextLng") == "en"
				? LanguagePost.en
				: LanguagePost.pt
		setLoading(true)
		let response: IArticles[] = []
		if (isNews) {
			response = await postService.listNews<IArticles[]>(language)
		} else if (isLegislation) {
			response = await postService.listLegislation<IArticles[]>(language)
		} else {
			response = await postService.list<IArticles[]>(language)
		}

		const [article, ...articles] = response
		setFirstArticle(article)
		setArticles(articles)
		setLoading(false)

		props.titleCallback()
	}

	const handleSubmit = async (data: IArticleFormData) => {
		data.userId = user.id
		if (data.id) {
			data.imageId = dataModal.imageId
			await postService.update(data)
		} else {
			await postService.create(data)
		}

		getArticles()
	}

	const handlePreview = async (data: IArticles) => {
		postService.savePreview(data)

		window.open("/preview", "_blank")
	}

	useEffect(() => {
		getArticles()
	}, [])

	return (
		<>
			<Banner
				titlePage={
					isNews
						? translate("banner.news")
						: isLegislation
						? translate("banner.legislation")
						: translate("banner.article")
				}
			/>

			<Container className="container">
				<UserLogged>
					<ButtonBox handleClick={openModal}>
						Adicionar Artigo <FaPlus />
					</ButtonBox>
					{showModal && (
						<ModalForm
							dataModal={dataModal}
							showModal={showModal}
							onCloseModal={closeModal}
							handleSubmit={handleSubmit}
							handlePreview={handlePreview}
							isLegislationForm={isLegislation}
						/>
					)}
				</UserLogged>

				<Row style={{ margin: "24px 0" }}>
					<Col md={8}>
						{loading ? (
							<ArticleCardLoad />
						) : (
							firstArticle && (
								<ArticleCard
									editArticle={editArticle}
									deleteArticle={deleteArticle}
									isFirst={true}
									{...firstArticle}
								/>
							)
						)}
					</Col>

					<Col xs="12" md={4} className="wrapper-facebook">
						<FacebookProvider appId={facebookData.id}>
							<Page href={facebookData.url} tabs="timeline" />
						</FacebookProvider>
					</Col>
				</Row>

				<Row style={{ margin: "24px 0" }}>
					{loading ? (
						<>
							<Col md={4}>
								<ArticleCardLoad />
							</Col>
							<Col md={4}>
								<ArticleCardLoad />
							</Col>
							<Col md={4}>
								<ArticleCardLoad />
							</Col>
						</>
					) : (
						articles.map(article => (
							<Col md={4} key={article.id} style={{ margin: "24px 0" }}>
								<ArticleCard
									editArticle={editArticle}
									deleteArticle={deleteArticle}
									{...article}
								/>
							</Col>
						))
					)}
				</Row>
			</Container>
		</>
	)
}

export default ArticlesComponent
